<template>
  <div id="Log">
    <div class="log">
      <el-scrollbar>
        <div class="log-main">
          <h1>更新日志 :</h1>
          <div class="log-box" v-for="(item, index) in logData " :key="index">
            <h2>{{ item.time }}</h2>
            <el-divider />
            <ul>
              <li v-for="log in item.logs">
                <span v-html="log"></span>
              </li>
            </ul>
          </div>
        </div>
      </el-scrollbar>
    </div>
  </div>
</template>
<script setup>
import { inject, provide, ref, onMounted, computed, reactive, nextTick, watchEffect } from 'vue';
import { ElMessageBox, ElMessage } from 'element-plus'
import { useStore } from 'vuex';
import IndexDB from '../untils/indexDB.js'
import webData from '../untils/websiteData.js'
const store = useStore()
const logData = reactive([
  {
    time: '07-07',
    logs: [
      `新增编辑/删除功能(数据只存在本地)`,
      '修复某些网站失效问题',
      `如果需要恢复数据请 <span id="reset" style="color:#409eff;  cursor: pointer;">点击此处</span> `
    ]
  },
  {
    time: '06-27',
    logs: [
      '优化移动端显示效果',
    ]
  },
  {
    time: '06-25',
    logs: [
      '新增更新日志页面',
      '优化网站导航页编辑按钮位置',
    ]
  },
  {
    time: '06-24',
    logs: [
      '网址导航页面,网页模块样式调整',
      '网址导航页面,新增编辑/删除按钮',
      '编辑功能已完成80%(已完成表单验证)',
      '修复某些网站失效问题',
    ]
  },
  {
    time: '06-22',
    logs: [
      '网址导航页面,网页模块样式调整',
      '网址导航新增 <b>Chat-Gpt</b> 分类',
    ]
  },
  {
    time: '06-21',
    logs: [
      `解决国内访问缓慢问题,并将网站网址更换为 <span style="color: #409eff;">https://doudounav.4everland.app</span>`,
    ]
  },
  {
    time: '06-18',
    logs: [
      `新增网站导航页`,
      `完成网站导航页样式/动画`,
      `网站导航页新增多个分类`,
      `解决顶部导航栏着色问题`,
      `将全局字体更换为 <b>MiSans</b>`,
      `适配移动端`,
    ]
  },
  {
    time: '06-11',
    logs: [
      `兜兜的导航项目正式启动`,
      `完成首页文字轮播`,
      `完成首页样式/动画`,
      `项目正式上线 <b>GitHub</b> 访问地址为: <span style="color: #409eff;">https://doudouli-youtang.github.io/DouDouNav</span>`,
      `解决移动端点击图标无法跳转问题`,
    ]
  },
])
nextTick(() => {
  const reset = document.getElementById("reset")
  reset.onclick = () => {
    ElMessageBox.confirm(
      `你确定要恢复全部数据吗?`,
      '警告',
      {
        type: 'warning',
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        dangerouslyUseHTMLString: true,
      }).then(async () => {
        store.commit('setData', webData.webArry)
        const dbName = 'DouDouDB', storeName = 'website';
        const db = await IndexDB.openDB(dbName, storeName, 1);
        const newData = { id: 1, data: JSON.stringify(webData.webArry) };
        IndexDB.updateDB(db, storeName, newData);
        ElMessage({
          message: '数据恢复成功',
          type: 'success',
        })
      }).catch(() => {

      })
  }
})
</script>

<style lang="less" scoped>
#Log {
  height: calc(100vh - 100px);
  margin: 0 auto;
  max-width: 1400px;


  .log-main {
    padding: 40px;
  }

  .log {
    margin: 40px;
    background-color: white;
    border-radius: 10px;
    height: calc(100% - 40px);

    .log-box {
      margin-bottom: 40px;
    }

    h1 {
      margin-top: 0;
      margin-bottom: 40px;
    }

    h2 {
      margin-bottom: 10px;
    }

    .el-divider {
      margin-top: 10px;
    }

    ul {
      padding-left: 20px;
      line-height: 30px;
    }
  }
}
</style>